.btn{
  /* btn:link is styling it as a link normally. btn:visited is styling the link after it has been visited*/
  &:link,
  &:visited, & {
    //Change for the <button> element
    border: none;
    cursor: pointer;

    text-transform: uppercase;
    text-decoration: none;
    padding: 1.5rem 4rem;
    display: inline-block;
    border-radius: 15rem;
    transition: all 0.2s; /*SECOND WAY OF DECLARING TRANSITION*/
    position: relative;
    font-size: $default-font-size;

  }

  &:hover{
    transform: translateY(-3px); /*Relative to its initial or link state*/
    box-shadow: 0 1rem 2rem rgba($color-black, 0.2);
  }
  
  /*btn:active is when the link click is active*/
  &:active{
    transform: translateY(-0.1rem); /*Relative to its initial or link state*/
    box-shadow: 0 0.5rem 1rem rgba($color-black, 0.2);
  }
  &--white{
    background-color: $color-white;
    color: $color-grey-dark;
    animation: whiteButton 0.5s ease-out 0.75s backwards;
    &:hover::after{
      background-color: $color-white;
      transform: scaleX(1.4) scaleY(1.6);
      opacity: 0;
    }
  }

  &--green {
    background-color: $color-primary;
    color: $color-white;
    animation: whiteButton 0.5s ease-out 0.75s backwards;
    &:hover::after{
      background-color: $color-primary;
      transform: scaleX(1.4) scaleY(1.6);
      opacity: 0;
    }
  }
  
  /*.btn::after re-creates btn just below btn*/
  &::after{
    content: '';
    display: inline-block;
    height: 100%;
    width: 100%;
    border-radius: 15rem;
    position: absolute;
    top: 0%;
    left: 0%;
    z-index: -1;
    transition: all 0.4s;
  }
}

.btn-text{
  &:link, &:visited{
    font-size: $default-font-size;
    color: $color-primary;
    display: inline-block;
    text-decoration: none;
    border-bottom: 1px solid $color-primary;
    padding: 3px;
    transition: all 0.5s ease-in-out;
    border-radius: 1rem;
  }
  &:hover{
    background-color: $color-primary;
    color: $color-white;
    transform: translateY(-0.3rem);
    box-shadow: 0.5rem 1.5rem 2rem rgba($color-black, 0.2);
  }
  &:active{
    transform: translateY(0);
    box-shadow: 0.5rem 1.5rem 2rem rgba($color-black, 0.2);
  }
}