body{
  font-family: 'Lato', sans-serif;
  line-height: 1.7;
  font-size: 400;
  color: $color-grey-dark;
}

.heading-primary{
  color: $color-white;
  text-transform: uppercase;
  backface-visibility: hidden; /*To fix the shakiness after an animation finishes*/
  margin-bottom: 6rem;


  &--main{
    display: block;
    font-size: 6rem;
    font-weight: 400;
    letter-spacing: 3.5rem;
    
    animation-name: moveInLeft;
    animation-duration: 1s;
    animation-timing-function: ease-out;
    
    @include respond(phone){
      letter-spacing: 1rem;
      font-size: 5rem;
    }

  }
  &--sub{
    display: block;
    font-size: 2rem;
    font-weight: 700;
    letter-spacing: 1.75rem;
  
    animation: moveInRight 1s ease-out;

    @include respond(phone){
      letter-spacing: .5rem;
    }
  }
}

.heading-secondary{
  display: inline-block;
  font-size: 3.5rem;
  text-transform: uppercase;
  font-weight: 700;
  background-image: linear-gradient(to right, $color-primary-light, $color-primary-dark);
  background-clip: text;
  -webkit-background-clip: text;
  color: transparent;
  animation: moveInLeft 2s ease-out;
  transition: all 0.2s;
  letter-spacing: 0.2rem;

  @include respond(tab-port){
    font-size: 3rem;
  }

  @include respond(phone){
    font-size: 2.5rem;
  }

  &:hover{
    transform: skewY(1deg) skewX(15deg) scale(1.1);
    text-shadow: 0.5rem 1rem 2.5rem rgba($color-black, 0.4);
  }
}

.heading-tertiary{
  font-size: 1.6rem;
  font-weight: 700;
  text-transform: uppercase;
}

.paragragh{
  font-size: 1.6rem;
  &:not(:last-child){
    margin-bottom: 3rem;
  }
}