.section-about{
  background-color: $color-grey-light-1;
  padding: 25rem 0;
  margin-top: -20vh;

  @include respond(tab-port){
    padding: 20rem 0;
  }
}

.section-features{
  padding: 20rem 0;
  background-image: 
  linear-gradient(
  to right bottom, 
  rgba($color-primary-light, 0.8), 
  rgba($color-primary-dark, 0.8)
  ), 
  url(../../../public/img/nat-4.jpg);
  background-size: cover;
  background-position: top;
  transform: skewY(-7deg);
  margin-top: -10rem;

  & > * { // & > .row OR & > div WILL DO SAME JOB IN THIS CASE.  > has to do with direct child
    transform: skewY(7deg);
  }

  @include respond(tab-port){
    padding: 5rem 0;
  }
}

.section-tours{
  background-color: $color-grey-light-1;
  padding: 25rem 0 15rem 0;
  margin-top: -10rem;

  @include respond(tab-port){
    padding: 20rem 0 10rem 0;
  }
}

.section-stories {
  position: relative;
  padding: 15rem 0;
  // background-color: $color-grey-light-1;

  @include respond(tab-port){
    padding: 10rem 0;
  }
  @include respond(phone){
    padding: 3rem 0;
  }
}

.section-book {
  padding: 15rem 0;
  background-image: linear-gradient(to right bottom, $color-primary-light, $color-primary-dark);

  @include respond(tab-port){
    padding: 10rem 0;
  }

}
.book {
  background-image: 
  linear-gradient(105deg, rgba($color-white, .9) 0%, rgba($color-white, .9) 50%, transparent 50%), 
  url('../../../public/img/nat-10.jpg');
  background-size: cover; //Using 100% instead of cover is thesame thing
  border-radius: $border-radius-3;
  box-shadow: 0 1.5rem 4rem rgba($color-black, 0.3);

  @include respond(tab-port){
    background-image: 
    linear-gradient(105deg, rgba($color-white, .9) 0%, rgba($color-white, .9) 70%, transparent 70%), 
    url('../../../public/img/nat-10.jpg');
    background-size: cover;
  }

  @include respond(phone){
    background-image: 
    linear-gradient(105deg, rgba($color-white, .9) 0%, rgba($color-white, .9) 100%), 
    url('../../../public/img/nat-10.jpg');
    background-size: cover;
  }

  // height: 50rem;
  &__form {
    width: 50%;
    padding: 6rem;

    @include respond(tab-port){
      width: 70%;
    }

    @include respond(phone){
      width: 100%;
    }
  }
}