.navigation {
  &__checkbox {
    display: none;
  }
  
  &__button {
    background-color: $color-white;
    height: 7rem;
    width: 7rem;
    border-radius: 50%;
    position: fixed;
    top: 6rem;
    right: 6rem;
    z-index: $z-index-200;
    box-shadow: 0 1rem 3rem rgba($color-black, .2);
    text-align: center;
    cursor: pointer;

    @include respond(tab-port){
      top: 4rem;
      right: 4rem;
    }
    @include respond(tab-port){
      top: 3rem;
      right: 3rem;
    }
  }

  &__background {
    height: 6rem;
    width: 6rem;
    border-radius: 50%;
    position: fixed;
    top: 6.5rem;
    right: 6.5rem;
    background-image: radial-gradient($color-primary-light, $color-primary-dark);
    z-index: $z-index-100;
    opacity: 0;
    transition: all 1s;

    @include respond(tab-port){
      top: 4.5rem;
      right: 4.5rem;
    }
    @include respond(tab-port){
      top: 3.5rem;
      right: 3.5rem;
    }
  }

  &__nav {
    height: 100vh;
    width: 100vw;
    position: fixed;
    z-index: $z-index-150;
    top: 0%;
    right: 0%;
    opacity: 0;
    // visibility: hidden;
    width: 0%;
    transition: all 1s cubic-bezier(.9,.37,0,1.23);
  }

  &__list {
    @include centerVP;
    list-style: none;

    @include respond(phone){
      width: 80%;
    }
    @include respond(mobile){
      width: 100%;
    }
  }

  &__item {
    margin: 1rem;
  }

  &__link {
    &:link, &:visited{
      display: inline-block;
      font-size: 3rem;
      font-weight: 300;
      padding: 1rem 2rem;
      color: $color-white;
      text-decoration: none;
      text-transform: uppercase;
      background-image: linear-gradient(120deg, transparent 0%, transparent 50%, $color-white 50% );
      background-size: 220%;
      transition: all 0.5s;

      span {
        display: inline-block;
        margin-right: 1.5rem;
      }
    }

    &:active, &:hover {
      background-position: 100%;
      color: $color-primary;
      transform: translateX(1rem);
    }
  }

  //FUNCTIONALITY
  &__checkbox:checked ~ &__nav {
    opacity: 1;
    visibility: visible;
    width: 100%;
  }

  &__checkbox:checked ~ &__background {
    transform: scale(80);
    opacity: 1;
  }

  //ICON
  &__icon{
    position: relative;
    margin-top: 3.5rem;

    &, &::before, &::after {
      width: 3rem;
      height: 2px; //We use pixels cos we dont want the height to change no matter the base font
      background-color: $color-grey-dark-3;
      display: inline-block;
    }

    &::before, &::after {
      content: '';
      position: absolute;
      left: 0;
      transition: all .3s;
    }

    &::before {
      top: -.8rem; //transform-origin:left >> is where you want a rotation to happen in an animation
    }

    &::after {
      top: .8rem;
    }
  }

  &__button:hover &__icon {
    &::before {
      transform: translateY(-.2rem);
    }
    &::after {
      transform: translateY(.2rem);
    }
  }

  &__checkbox:checked + &__button &__icon {
    background-color: transparent;
  }
  
  &__checkbox:checked + &__button &__icon::before {

    transform: translateY(.8rem) rotate(135deg);
  }

  &__checkbox:checked + &__button &__icon::after{
    transform: translateY(-.8rem) rotate(-135deg);
  }
}
